.remove-item {
  display: block;
  padding: 3px 8px;
  font-size: 10px;
  font-weight: 700;
  border: 1px solid #e5e5e5;
  background-color: #ddd;
  color: #888;
  border-radius: 4px;
  cursor: pointer;
  transition: all .3s;

  &:hover {
    background-color: #505050;
    color: white;
  }
}