@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700);
html,
body {
  height: 100%; }

body {
  font-family: 'Montserrat', sans-serif; }

.App-main {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 90vh; }

.App-nav {
  padding: 1.5rem;
  margin-bottom: 3rem;
  background-color: #16a088; }
  .App-nav .nav-link {
    font-size: .9rem;
    color: white;
    padding: .5rem 0;
    margin: 0 1rem;
    border-bottom: 2px solid transparent;
    transition: all .2s ease-in-out; }
    .App-nav .nav-link:hover {
      border-bottom: 2px solid white; }
    .App-nav .nav-link.active {
      border-bottom: 2px solid white; }

.nav-item--logo {
  margin: 0 !important;
  margin-right: 40px; }
  .nav-item--logo .nav-link {
    padding: 0;
    border-bottom: 2px solid transparent !important; }

.App-login {
  display: flex;
  background-color: #f5f5f5; }

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px; }

.form-signin .checkbox {
  font-weight: 400; }

.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px; }

.form-signin .form-control:focus {
  z-index: 2; }

.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0; }

.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0; }

.products-page-title {
  margin-bottom: 2rem; }

.product-item {
  margin-bottom: 2rem; }

.product-grid4 {
  position: relative;
  text-align: center;
  border-radius: 5px;
  overflow: hidden;
  z-index: 1;
  transition: all .3s ease 0s;
  border: 1px solid rgba(0, 0, 0, 0.125);
  background-color: white; }
  .product-grid4:hover .product-new-label {
    opacity: 0; }
  .product-grid4:hover .add-to-cart {
    border: 1px solid transparent;
    background: #16a088;
    color: #fff; }
  @media only screen and (max-width: 990px) {
    .product-grid4 {
      margin-bottom: 30px; } }
  .product-grid4 .product-image4 {
    position: relative; }
  .product-grid4 .product-discount-label,
  .product-grid4 .product-new-label {
    color: #fff;
    background-color: #16a088;
    font-size: 13px;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 45px;
    height: 45px;
    width: 45px;
    border-radius: 50%;
    position: absolute;
    left: 10px;
    top: 15px;
    transition: all .3s; }
  .product-grid4 .product-discount-label {
    left: auto;
    right: 10px;
    background-color: #d7292a; }
  .product-grid4 .product-content {
    padding: 25px; }
  .product-grid4 .title {
    font-size: 18px;
    font-weight: 400;
    text-transform: capitalize;
    margin: 10px 0 20px;
    transition: all .3s ease 0s; }
    .product-grid4 .title a {
      color: #222; }
      .product-grid4 .title a:hover {
        color: #16a088; }
  .product-grid4 .price {
    color: #16a088;
    font-size: 17px;
    font-weight: 700;
    margin: 0 2px 15px 0;
    display: block; }
  .product-grid4 .add-to-cart {
    border: 1px solid #e5e5e5;
    display: inline-block;
    padding: 10px 20px;
    color: #888;
    font-weight: 600;
    font-size: 14px;
    border-radius: 4px;
    cursor: pointer;
    transition: all .3s; }
    .product-grid4 .add-to-cart:hover {
      background-color: #505050;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); }

@media only screen and (max-width: 990px) {
  .product-grid4 {
    margin-bottom: 30px; } }

.needs-validation {
  margin-bottom: 5rem; }

.remove-item {
  display: block;
  padding: 3px 8px;
  font-size: 10px;
  font-weight: 700;
  border: 1px solid #e5e5e5;
  background-color: #ddd;
  color: #888;
  border-radius: 4px;
  cursor: pointer;
  transition: all .3s; }
  .remove-item:hover {
    background-color: #505050;
    color: white; }

